"use client";

import Image from "next/image";
import Masonry from "react-masonry-css";
import { IMAGE_BASE_URL } from "@/lib/const";
import { useSuspenseQuery } from "@tanstack/react-query";
import { homeOptions } from "@/lib/api/home";
import { BlogCard } from "@/components/blog/card";
export default function PageClient({
  isMobile
}) {
  const {
    data
  } = useSuspenseQuery(homeOptions);
  const breakpointColumnsObj = {
    default: 3,
    1440: 4,
    1100: 3,
    700: 2,
    500: 1
  };
  const imagesSorted = data.data[0].gallery_item?.filter(image => image.order);
  const imagesUnSorted = data.data[0].gallery_item?.filter(image_0 => !image_0.order);
  const ImageComponent = ({
    image: image_1,
    index
  }) => <div key={`image${image_1.id}`} className="mb-4">
      <Image src={IMAGE_BASE_URL + (image_1.image?.url || "")} width={image_1.image?.width || 1000} height={image_1.image?.height || 1000} alt={image_1.image?.alt || "Gallery image"} className="rounded-xl w-full h-auto" priority={index < 4} loading={index < 4 ? "eager" : "lazy"} />
    </div>;
  return <>
      {isMobile ?
    // <div className="flex flex-col gap-4">
    //   {/* <Image
    //     src="/ratings.webp"
    //     width={2000}
    //     height={2000}
    //     alt="Ratings"
    //     className="my-8 w-full"
    //   /> */}
    //   {imagesSorted
    //     .sort((a, b) => a.order - b.order)
    //     .map((image, idx) => (
    //       <ImageComponent key={image.id} image={image} index={idx} />
    //     ))}
    //   {imagesUnSorted.map((image, idx) => (
    //     <ImageComponent key={image.id} image={image} index={idx} />
    //   ))}
    // </div>
    <Masonry breakpointCols={{
      default: 2,
      500: 2
    }} className="flex w-auto" columnClassName="bg-clip-padding px-2">
          {/* <Image
           src="/ratings.webp"
           width={2000}
           height={2000}
           alt="Ratings"
           className="my-8 w-full"
           /> */}

          {imagesSorted.sort((a, b) => a.order - b.order).map((image_2, idx) => <ImageComponent key={image_2.id} image={image_2} index={idx} />)}
          {imagesUnSorted.map((image_3, idx_0) => <ImageComponent key={image_3.id} image={image_3} index={idx_0} />)}
        </Masonry> : <Masonry breakpointCols={breakpointColumnsObj} className="flex w-auto" columnClassName="bg-clip-padding px-2">
          {/* <Image
            src="/ratings.webp"
            width={2000}
            height={2000}
            alt="Ratings"
            className="my-8 w-full"
           /> */}
          {imagesSorted.sort((a_0, b_0) => a_0.order - b_0.order).map((image_4, idx_1) => <ImageComponent key={image_4.id} image={image_4} index={idx_1} />)}
          {imagesUnSorted.map((image_5, idx_2) => <ImageComponent key={image_5.id} image={image_5} index={idx_2} />)}
        </Masonry>}
      <div className="flex flex-col p-8 gap-8">
        {data.data[0].blogs.map(b_1 => <BlogCard key={b_1.id} blogPost={b_1} />)}
      </div>
    </>;
}